window.dom = {
    /*åˆ›å»ºä¸€ä¸ªèŠ‚ç‚¹*/
    create(string) {
        const container = document.createElement("template");/*templateç”¨æ¥æ¸…é™¤å­—ç¬¦ä¸²çš„ç©ºæ ¼ */
        container.innerHTML = string.trim();    /*trim()ç”¨æ¥é™¤å»ç©ºæ ¼*/

        return container.content.firstChild;  //è¿”å›containerçš„å†…å®¹çš„ç¬¬ä¸€ä¸ªå­©å­
    },
    /*æ–°å¢ä¸€ä¸ªå¼Ÿå¼Ÿ*/
    after(node, node2) {
        node.parentNode.insertBefore(node2, node.nextSibling);//æ‰¾åˆ°èŠ‚ç‚¹çš„çˆ¸çˆ¸ï¼Œè°ƒç”¨insertBeforeçš„æ–¹æ³•ï¼ŒæŠŠnode2æ’å…¥åˆ°ä¸‹ä¸€ä¸ªèŠ‚ç‚¹çš„å‰é¢
    },
    /*æ–°å¢ä¸€ä¸ªå“¥å“¥*/
    before(node, node2) {
        node.parentNode.insertBefore(node2, node);
    },
    // /* æ–°å¢ä¸€ä¸ªå„¿å­*/
    append(parent, node) {
        parent.appendChild(node)
    },
    // /* æ–°å¢ä¸€ä¸ªçˆ¸çˆ¸*/
    wrap(node, parent) {
        dom.before(node, parent)//è°ƒç”¨ä¸Šé¢çš„beforeæ–¹æ³•ï¼Œä½¿å¾—nodeæ˜¯parentçš„å¼Ÿå¼Ÿ
        dom.append(parent, node)//è°ƒç”¨ä¸Šé¢çš„appendçš„æ–¹æ³•ï¼ŒæŠŠnodeå˜æˆparentçš„å„¿å­
    },
    // /* å•çº¯çš„åˆ é™¤èŠ‚ç‚¹*/
    remove(node) {
        node.parentNode.removeChild(node)//èŠ‚ç‚¹çš„çˆ¸çˆ¸åˆ é™¤è¿™ä¸ªå…ƒç´ 
        return node//è¿”å›è¿™ä¸ªèŠ‚ç‚¹ï¼Œå¯ä»¥ä¿ç•™è¿™ä¸ªèŠ‚ç‚¹çš„å¼•ç”¨
    },
    // /*åˆ é™¤çš„èŠ‚ç‚¹æ”¾åœ¨æ•°ç»„é‡Œ*/
    empty(node) {
        const array = []//å£°æ˜ä¸€ä¸ªæ•°ç»„ï¼Œç”¨æ¥å­˜æ”¾åˆ é™¤çš„å…ƒç´ 
        let x = node.firstChild//å£°æ˜xç­‰äºèŠ‚ç‚¹çš„ç¬¬ä¸€ä¸ªå­©å­
        while (x) {
            array.push(dom.remove(node.firstChild))//è°ƒç”¨dom.removeçš„æ–¹æ³•ï¼Œåˆ é™¤ç¬¬ä¸€ä¸ªå­©å­ï¼Œå¹¶æŠŠå®ƒpushåˆ°æ•°ç»„é‡Œ
            x = node.firstChild//æ­¤æ—¶ï¼Œxç­‰äºä¹‹å‰çš„ç¬¬äºŒä¸ªå­©å­
        }
        return array
    },
    /*è®¾ç½®æˆ–è¯»å–titleçš„å€¼ */
    attr(node, name, value) { // é‡è½½
        if (arguments.length === 3) {
            node.setAttribute(name, value)//å‚æ•°ä¸º3ï¼Œåˆ™æ˜¯è®¾ç½®titleçš„å€¼
        } else if (arguments.length === 2) {
            return node.getAttribute(name)//å‚æ•°ä¸º3ï¼Œåˆ™æ˜¯è¯»å–titleçš„å€¼
        }
    },
    /*è¯»æˆ–å†™æ–‡æœ¬çš„å†…å®¹ */
    text(node, string) { // é€‚é…
        if (arguments.length === 2) { //{å‚æ•°ä¸º2ï¼Œåˆ™æ˜¯è®¾ç½®æ–‡æœ¬çš„å€¼
            if ('innerText' in node) {
                node.innerText = string//åˆ¤æ–­æµè§ˆå™¨æ˜¯å¦æ”¯æŒinnerText
            } else {
                node.textContent = string//ä¸æ”¯æŒå°±ç”¨textContent
            }
        } else if (arguments.length === 1) {//{å‚æ•°ä¸º2ï¼Œåˆ™æ˜¯è¯»å–æ–‡æœ¬çš„å€¼
            if ('innerText' in node) {
                return node.innerText//åŒä¸Š
            } else {
                return node.textContent//åŒä¸Š
            }
        }
    },
    /*è¯»å†™htmlçš„å†…å®¹ */
    html(node, string) {
        if (arguments.length === 2) {//{å‚æ•°ä¸º2ï¼Œåˆ™æ˜¯è®¾ç½®htmlçš„å€¼
            node.innerHTML = string
        } else if (arguments.length === 1) {
            return node.innerHTML//{å‚æ•°ä¸º2ï¼Œåˆ™æ˜¯è¯»å–htmlçš„å€¼
        }
    },
    /*è¯»å†™styleçš„å±æ€§ */
    style(node, name, value) {
        if (arguments.length === 3) {
            // dom.style(div, 'color', 'red')
            node.style[name] = value//å‚æ•°ä¸º3ï¼Œè¡¨ç¤ºæ˜¯è®¾ç½®styleçš„å€¼
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {//åˆ¤æ–­nameæ˜¯ä¸æ˜¯å­—ç¬¦ä¸²ï¼Œæ˜¯çš„è¯å°±æ˜¯è¯»å–styleçš„å±æ€§
                // dom.style(div, 'color')
                return node.style[name]
            } else if (name instanceof Object) {//åˆ¤æ–­nameæ˜¯ä¸æ˜¯å¯¹è±¡
                // dom.style(div, {color: 'red'})
                const object = name
                for (let key in object) {
                    node.style[key] = object[key]//éå†å¯¹è±¡ï¼Œä»¥å¯¹è±¡çš„å½¢å¼è®¾ç½®style
                }
            }
        }
    },
    /*æ·»åŠ class */
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        remove(node, className) {
            node.classList.remove(className)
        },
        has(node, className) {//åˆ¤æ–­æœ‰æ²¡æœ‰è¿™ä¸ªå…ƒç´ 
            return node.classList.contains(className)//
        }
    },
    /*æ·»åŠ äº‹ä»¶ç›‘å¬ */
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    /*ç§»é™¤äº‹ä»¶ç›‘å¬ */
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    /*è·å–æ ‡ç­¾æˆ–è€…å¾ˆå¤šæ ‡ç­¾ */
    find(selector, scope) {
        return (scope || document).querySelectorAll(selector)//scopeé‡Œæ²¡æœ‰ï¼Œå†å»documenté‡Œæ‰¾
    },
    /*è·å–çˆ¶å…ƒç´  */
    parent(node) {
        return node.parentNode
    },
    /*è·å–å­å…ƒç´  */
    children(node) {
        return node.children
    },
    /* è·å–å…„å¼Ÿå§å¦¹å…ƒç´ */
    siblings(node) {
        return Array.from(node.parentNode.children)//æŠŠnodeçš„çˆ¶äº²çš„æ‰€æœ‰å„¿å­æ‹¿å‡ºæ¥ï¼Œå˜æˆæ•°ç»„ï¼ŒåŒæ—¶å¹¶ä¸”è¿‡æ»¤æ‰è‡ªå·±
            .filter(n => n !== node)
    },
    /*è·å–å¼Ÿå¼Ÿå…ƒç´  */
    next(node) {
        let x = node.nextSibling
        while (x && x.nodeType === 3) {//åˆ¤æ–­xæ˜¯ä¸æ˜¯æ–‡æœ¬ï¼Œæ–‡æœ¬çš„è¯å°±æ‰¾ä¸‹ä¸€ä¸ª
            x = x.nextSibling
        }
        return x
    },
    /*è·å–å“¥å“¥å…ƒç´  */
    previous(node) {
        let x = node.previousSibling
        while (x && x.nodeType === 3) {//åˆ¤æ–­xæ˜¯ä¸æ˜¯æ–‡æœ¬ï¼Œæ–‡æœ¬çš„è¯å°±æ‰¾ä¸Šä¸€ä¸ª
            x = x.previousSibling
        }
        return x
    },
    /*éå†æ‰€æœ‰èŠ‚ç‚¹ */
    each(nodeList, fn) {
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    /*è·å–æ’è¡Œç¬¬å‡  */
    index(node) {
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }
};